/**
 * English-only translation function
 * @param {string} key Translation key
 * @param {Object} args Optional arguments for substitution
 * @returns {string} English translation or key if not found
 */
function translate(key, args = {}) {
    let translation = ENGLISH_TRANSLATIONS[key] || key;
    
    // Handle argument substitution like {{s}}
    if (args && typeof args === 'object') {
        for (const [placeholder, value] of Object.entries(args)) {
            translation = translation.replace(new RegExp(`{{${placeholder}}}`, 'g'), value);
        }
    }
    
    return translation;
}

// Set dummy locale for compatibility
var currentLocale = "en";

function setLocale(locale) {
    currentLocale = "en"; // Always English
    return true;
}

exports.__ = translate;