const { __ } = require("../browser_i18n");

/**
 * Shared Import Validation Utility
 * Validates shopping list import data structure
 */
class ImportValidator {
    static VALID_STORES = ['aldi', 'coles', 'woolies', 'iga'];

    /**
     * Validate imported data structure and content
     * @param {Object} data - Parsed import data
     * @returns {Object} - { valid: boolean, error: string }
     */
    static validate(data) {
        // Check version field
        if (!data.version) {
            return { valid: false, error: "Missing version field" };
        }

        // Check version compatibility
        if (data.version !== "1.0") {
            return { valid: false, error: `Unsupported version: ${data.version}` };
        }

        // Check list object
        if (!data.list || typeof data.list !== 'object') {
            return { valid: false, error: "Missing or invalid list data" };
        }

        const list = data.list;

        // Validate required fields
        if (!list.name || typeof list.name !== 'string') {
            return { valid: false, error: "Missing or invalid list name" };
        }

        if (!Array.isArray(list.stores)) {
            return { valid: false, error: "Missing or invalid stores array" };
        }

        if (!Array.isArray(list.items)) {
            return { valid: false, error: "Missing or invalid items array" };
        }

        // Validate stores
        for (const store of list.stores) {
            if (!this.VALID_STORES.includes(store)) {
                return { valid: false, error: `Invalid store name: ${store}` };
            }
        }

        // Validate items structure
        for (let i = 0; i < list.items.length; i++) {
            const item = list.items[i];

            // Check mainRef
            if (!item.mainRef || typeof item.mainRef !== 'object') {
                return { valid: false, error: `Item ${i}: missing mainRef` };
            }

            if (!item.mainRef.store || !item.mainRef.id) {
                return { valid: false, error: `Item ${i}: invalid mainRef structure` };
            }

            if (!this.VALID_STORES.includes(item.mainRef.store)) {
                return { valid: false, error: `Item ${i}: invalid store in mainRef: ${item.mainRef.store}` };
            }

            // Check alternativeRefs
            if (!Array.isArray(item.alternativeRefs)) {
                return { valid: false, error: `Item ${i}: invalid alternativeRefs` };
            }

            for (let j = 0; j < item.alternativeRefs.length; j++) {
                const altRef = item.alternativeRefs[j];

                if (!altRef.store || !altRef.id) {
                    return { valid: false, error: `Item ${i}, alt ${j}: invalid reference structure` };
                }

                if (!this.VALID_STORES.includes(altRef.store)) {
                    return { valid: false, error: `Item ${i}, alt ${j}: invalid store: ${altRef.store}` };
                }

                // Validate quantity field (optional, but must be positive number if present)
                if (altRef.quantity !== undefined) {
                    if (typeof altRef.quantity !== 'number' || altRef.quantity <= 0) {
                        return { valid: false, error: `Item ${i}, alt ${j}: quantity must be a positive number` };
                    }
                }
            }

            // Validate selected field (optional)
            if (item.selected && !['main', 'alt'].includes(item.selected)) {
                return { valid: false, error: `Item ${i}: invalid selected value: ${item.selected}` };
            }
        }

        // Validate timestamps (optional but should be ISO format if present)
        if (list.createdAt && isNaN(Date.parse(list.createdAt))) {
            return { valid: false, error: "Invalid createdAt timestamp" };
        }

        if (list.updatedAt && isNaN(Date.parse(list.updatedAt))) {
            return { valid: false, error: "Invalid updatedAt timestamp" };
        }

        return { valid: true };
    }
}

/**
 * Shopping Lists Overview Manager
 * Manages the list of all shopping lists with create/delete operations
 */
class ShoppingListsManager {
    constructor() {
        this.lists = [];
        this.init();
    }

    init() {
        this.loadLists();
        this.setupEventListeners();
        this.render();
    }

    /**
     * Load all shopping lists from localStorage
     */
    loadLists() {
        this.lists = [];

        // Scan localStorage for shopping_list_ keys
        for (let i = 0; i < localStorage.length; i++) {
            const key = localStorage.key(i);
            if (key && key.startsWith('shopping_list_')) {
                try {
                    const data = JSON.parse(localStorage.getItem(key));
                    this.lists.push(data);
                } catch (e) {
                    console.error(`Failed to load list ${key}:`, e);
                }
            }
        }

        // Sort by createdAt (newest first)
        this.lists.sort((a, b) => new Date(b.createdAt) - new Date(a.createdAt));
    }

    /**
     * Set up event listeners
     */
    setupEventListeners() {
        // New list buttons
        const newListBtn = document.getElementById('newListBtn');
        const createFirstBtn = document.getElementById('createFirstBtn');
        const importHeaderBtn = document.getElementById('importHeaderBtn');
        const importFirstBtn = document.getElementById('importFirstBtn');
        const importFileInput = document.getElementById('import-file-input');
        const cancelBtn = document.getElementById('cancelBtn');
        const createBtn = document.getElementById('createBtn');
        const newListModal = document.getElementById('newListModal');
        const listNameInput = document.getElementById('listNameInput');

        // Only add event listeners if elements exist (defensive programming)
        if (newListBtn) {
            newListBtn.addEventListener('click', () => this.showNewListModal());
        }
        if (createFirstBtn) {
            createFirstBtn.addEventListener('click', () => this.showNewListModal());
        }

        // Import buttons
        if (importHeaderBtn) {
            importHeaderBtn.addEventListener('click', () => this.triggerImport());
        }
        if (importFirstBtn) {
            importFirstBtn.addEventListener('click', () => this.triggerImport());
        }

        // File input handler
        if (importFileInput) {
            importFileInput.addEventListener('change', (e) => this.handleImportFile(e));
        }

        // Modal buttons
        if (cancelBtn) {
            cancelBtn.addEventListener('click', () => this.hideNewListModal());
        }
        if (createBtn) {
            createBtn.addEventListener('click', () => this.createList());
        }

        // Close modal on overlay click
        if (newListModal) {
            newListModal.addEventListener('click', (e) => {
                if (e.target.id === 'newListModal') {
                    this.hideNewListModal();
                }
            });
        }

        // Enter key in name input
        if (listNameInput) {
            listNameInput.addEventListener('keypress', (e) => {
                if (e.key === 'Enter') {
                    this.createList();
                }
            });
        }
    }

    /**
     * Show new list modal
     */
    showNewListModal() {
        const modal = document.getElementById('newListModal');
        const input = document.getElementById('listNameInput');

        if (!modal || !input) return;

        modal.classList.remove('hidden');
        input.value = '';
        document.querySelectorAll('.store-checkbox').forEach(cb => cb.checked = false);
        setTimeout(() => input.focus(), 100);
    }

    /**
     * Hide new list modal
     */
    hideNewListModal() {
        const modal = document.getElementById('newListModal');
        if (modal) {
            modal.classList.add('hidden');
        }
    }

    /**
     * Create new shopping list
     */
    createList() {
        const nameInput = document.getElementById('listNameInput');
        const name = nameInput.value.trim();

        if (!name) {
            alert(__("Please enter a list name"));
            return;
        }

        // Get selected stores
        const stores = Array.from(document.querySelectorAll('.store-checkbox:checked'))
            .map(cb => cb.value);

        if (stores.length === 0) {
            alert(__("Please select at least one store"));
            return;
        }

        // Create list object
        const id = 'sl_' + Date.now();
        const list = {
            id,
            name,
            stores,
            items: [],
            createdAt: new Date().toISOString()
        };

        // Save to localStorage
        localStorage.setItem(`shopping_list_${id}`, JSON.stringify(list));

        // Reload and render
        this.loadLists();
        this.render();
        this.hideNewListModal();

        // Navigate to detail view
        window.location.href = `shopping-list-detail.html?id=${id}`;
    }

    /**
     * Delete shopping list
     */
    deleteList(listId) {
        if (!confirm(__("Delete this list?"))) {
            return;
        }

        localStorage.removeItem(`shopping_list_${listId}`);
        this.loadLists();
        this.render();
    }

    /**
     * Update storage warning message based on list count
     */
    updateStorageWarning() {
        const warningContent = document.getElementById('storageWarningContent');

        if (!warningContent) {
            return;
        }

        const listCount = this.lists.length;

        if (listCount === 0) {
            warningContent.innerHTML = `<p>${__("Shopping_lists_are_stored_locally_on_this_device_only.")}</p>`;
        } else if (listCount === 1) {
            warningContent.innerHTML = `<p>${__("Your_shopping_list_is_stored_locally_on_this_device_only.")}</p>`;
        } else {
            warningContent.innerHTML = `<p>${__("Your")} ${listCount} ${__("shopping_lists_are_stored_locally_on_this_device_only.")}</p>`;
        }
    }

    /**
     * Render lists
     */
    render() {
        const container = document.getElementById('listsContainer');
        const emptyState = document.getElementById('emptyState');

        // Guard: If elements don't exist, this is not the shopping list page
        if (!container || !emptyState) {
            return;
        }

        // Update storage warning message
        this.updateStorageWarning();

        if (this.lists.length === 0) {
            container.innerHTML = '';
            emptyState.classList.remove('hidden');
            return;
        }

        emptyState.classList.add('hidden');

        container.innerHTML = this.lists.map(list => {
            const itemCount = list.items ? list.items.length : 0;
            const storeNames = list.stores.map(s => {
                const names = {
                    woolies: 'Woolworths',
                    coles: 'Coles',
                    aldi: 'ALDI',
                    iga: 'IGA'
                };
                return names[s] || s;
            }).join(', ');

            const createdDate = new Date(list.createdAt).toLocaleDateString();

            return `
                <div class="border rounded-lg p-4 hover:shadow-md transition-shadow">
                    <div class="flex items-start justify-between">
                        <div class="flex-1 cursor-pointer" onclick="window.location.href='shopping-list-detail.html?id=${list.id}'">
                            <h3 class="text-lg font-semibold mb-1">${this.escapeHtml(list.name)}</h3>
                            <div class="text-sm text-gray-600 space-y-1">
                                <div>📦 ${itemCount} ${itemCount === 1 ? __('item') : __('items')}</div>
                                <div>🏪 ${storeNames}</div>
                                <div>📅 ${createdDate}</div>
                            </div>
                        </div>
                        <div class="flex gap-2 ml-4">
                            <button onclick="window.location.href='shopping-list-detail.html?id=${list.id}'"
                                    class="px-3 py-2 text-primary hover:bg-primary/10 rounded-lg transition-colors min-h-[44px]"
                                    title="${__('View list')}">
                                <svg class="w-5 h-5" fill="none" stroke="currentColor" viewBox="0 0 24 24">
                                    <path stroke-linecap="round" stroke-linejoin="round" stroke-width="2" d="M15 12a3 3 0 11-6 0 3 3 0 016 0z"/>
                                    <path stroke-linecap="round" stroke-linejoin="round" stroke-width="2" d="M2.458 12C3.732 7.943 7.523 5 12 5c4.478 0 8.268 2.943 9.542 7-1.274 4.057-5.064 7-9.542 7-4.477 0-8.268-2.943-9.542-7z"/>
                                </svg>
                            </button>
                            <button onclick="shoppingListsManager.deleteList('${list.id}')"
                                    class="px-3 py-2 text-red-600 hover:bg-red-50 rounded-lg transition-colors min-h-[44px]"
                                    title="${__('Delete list')}">
                                <svg class="w-5 h-5" fill="none" stroke="currentColor" viewBox="0 0 24 24">
                                    <path stroke-linecap="round" stroke-linejoin="round" stroke-width="2" d="M19 7l-.867 12.142A2 2 0 0116.138 21H7.862a2 2 0 01-1.995-1.858L5 7m5 4v6m4-6v6m1-10V4a1 1 0 00-1-1h-4a1 1 0 00-1 1v3M4 7h16"/>
                                </svg>
                            </button>
                        </div>
                    </div>
                </div>
            `;
        }).join('');
    }

    /**
     * Trigger file picker for import
     */
    triggerImport() {
        document.getElementById('import-file-input').click();
    }

    /**
     * Handle file selection for import
     * @param {Event} event - File input change event
     */
    handleImportFile(event) {
        const file = event.target.files[0];

        if (!file) {
            return;
        }

        // Validate file type
        if (!file.name.endsWith('.json')) {
            alert(__("Please select a JSON file"));
            event.target.value = ''; // Reset input
            return;
        }

        // Read file
        const reader = new FileReader();

        reader.onload = (e) => {
            try {
                const content = e.target.result;
                this.importList(content);
            } catch (error) {
                console.error("File read failed:", error);
                alert(__("Failed to read file"));
            } finally {
                event.target.value = ''; // Reset input for next use
            }
        };

        reader.onerror = () => {
            alert(__("Failed to read file"));
            event.target.value = ''; // Reset input
        };

        reader.readAsText(file);
    }

    /**
     * Import shopping list from JSON content
     * Creates a new list and redirects to detail page
     * @param {string} jsonContent - JSON string content
     */
    importList(jsonContent) {
        try {
            // Parse JSON
            let importData;
            try {
                importData = JSON.parse(jsonContent);
            } catch (parseError) {
                alert(__("Invalid JSON format"));
                return;
            }

            // Validate import data using shared validator
            const validationResult = ImportValidator.validate(importData);

            if (!validationResult.valid) {
                alert(__(validationResult.error));
                console.error("Validation failed:", validationResult.error);
                return;
            }

            // Confirm before importing
            const confirmMessage = __(`Import "${importData.list.name}" as a new list?`);
            if (!confirm(confirmMessage)) {
                return;
            }

            // Create NEW list with unique ID
            const newListId = 'sl_' + Date.now();
            const newList = {
                id: newListId,
                name: importData.list.name,
                stores: importData.list.stores,
                items: importData.list.items,
                createdAt: new Date().toISOString(),
                updatedAt: new Date().toISOString()
            };

            // Save new list to localStorage
            try {
                localStorage.setItem(`shopping_list_${newListId}`, JSON.stringify(newList));
            } catch (error) {
                if (error.name === 'QuotaExceededError') {
                    alert(__("Storage full! Please delete old lists to make space."));
                    return;
                }
                throw error;
            }

            console.log(`Created new list from import: ${newList.name} (${newList.items.length} items)`);

            // Redirect to newly created list
            window.location.href = `shopping-list-detail.html?id=${newListId}`;

        } catch (error) {
            console.error("Import failed:", error);
            alert(__("Failed to import list"));
        }
    }

    /**
     * Escape HTML to prevent XSS
     */
    escapeHtml(text) {
        const div = document.createElement('div');
        div.textContent = text;
        return div.innerHTML;
    }
}

// Initialize manager and make it globally accessible for onclick handlers
window.shoppingListsManager = new ShoppingListsManager();

// Export for use in other modules
module.exports = { ShoppingListsManager, ImportValidator };
